const LEFT_SIDE_CAPTIONS = ["ASSERTIVENESS", "TOUGH MINDED", "RELAXED"];
const RIGHT_SIDE_CAPTIONS = [
	"SELF-ASSURED",
	"ENTHUSIASTIC",
	"EMOTIONAL QUOTIENT",
];

function createHTML(data) {
	return {
		__html: data,
	};
}

const LockedReportSection = (props) => {
	const lockedItems = [];
	for (var i = 0; i < props.count; i++) {
		lockedItems.push(<p>{props.content}</p>);
	}
	return (
		<div className="report--section">
			<p dangerouslySetInnerHTML={createHTML(props.content)} />
			<div className="report--section-locked">
				<div className="report--section-children">{lockedItems}</div>
				<div className="report--section-overlay flex-center flex-column">
					<p className="font-weight-bold h6">
						{props.subContent || "To Know more"}
					</p>
					<Link
						className="btn btn-primary btn-sm"
						// onClick={(props) => openPayment(props)}
						to="/checkout"
					>
						Unlock full report
					</Link>
				</div>
			</div>
		</div>
	);
};

const ReportSection = ({
	title,
	content,
	isHTML,
	isLocked,
	subContent,
	totalCount = 3,
	header,
}) => {
	return (
		<section className="report-section">
			<h3 className="reporSubtHeading"><span><img src="assets/left_arrow_icon.jpg" alt="about-us"/></span>{title}</h3>
			{header ? <h4 className="captionHeading">{header}</h4> : ""}
			<ul>
				{
					// If isHTML is true, always render it directly from the server via dangerouslySetInnerHTML
					// otherwise  the value should be an Array or a String Value,
					//in that case either map or render the  content
					!isLocked ? (
						isHTML ? (
							Array.isArray(content) ? (
								content.map((item, index) => (
									<li key={index}>
										<div
											dangerouslySetInnerHTML={createHTML(
												item
											)}
										/>
									</li>
								))
							) : (
								<div
									dangerouslySetInnerHTML={createHTML(
										content
									)}
								/>
							)
						) : Array.isArray(content) ? (
							content.map((item, index) => (
								<li key={index}>{item}</li>
							))
						) : (
							<p>{content}</p>
						)
					) : (
						<LockedReportSection
							count={3}
							content={content}
							subContent={subContent}
						/>
					)
				}
			</ul>
		</section>
	);
};

